/* content */

.container {
  margin-top: 80px;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 15px;
}

.contentText {
  font-size: larger;
  color: #3b4890;
  font-weight: bolder;
  justify-content: center;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 2px solid #3b4890;
  width: 1200px;
}

.contentImg {
  padding-top: 150px;
  padding-bottom: 150px;
  background-image: url(../asset/img/symbol_ajou.jpg);
  background-size: cover;
  background-position: top;
  color: white;
  font-size: 50px;
}
.contentImg a {
  text-decoration-line: none;
  color: white;
}

/* announcement css */

.announcement {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 200px;
}
.announcementObject {
  display: flex;
  justify-content: center;
}
.announcementImg {
  width: 400px;
  height: 500px;
  object-fit: cover;
  margin: 0px 30px 0px 30px;
}
